.noticesListContainer {
  margin: 0 auto;
}
.noticeList {
  margin-top: 24px;
  margin-bottom: 36px;
}

.addPetBtn {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 200;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.33;
  color: #ffffff;
  background: #54adff;
  border: none;
  box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
  transition: background-image 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.addPetBtn:hover,
.addPetBtn:focus {
  background-image: linear-gradient(290.46deg, #419ef1 0%, #9bd0ff 107.89%);
}
.addPetBtnIcon {
  margin-bottom: 8px;
}

@media screen and (max-width: 767px) {
  .noticesListContainer {
    max-width: 480px;
  }
}
@media screen and (min-width: 768px) {
  .noticesListContainer {
    width: 704px;
    margin-top: 40px;
  }
  /* тут перекриває модалку */
  .noticeList {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
    justify-content: center;
  }
}

@media screen and (min-width: 1280px) {
  .noticesListContainer {
    width: 1248px;
    margin-top: 42px;
  }

  .noticeList {
    gap: 32px;
  }
}
